import store from '@/store/index.js';
import { getSearchLyric } from '@/request/api/search.js';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 默认每次请求数量
      limit: 20,
      // 决定请求返回的数据范围
      offset: 0,
      // 当前页
      currentPage: 1,
      // 查到的歌词数量
      songCount: 0,
      // 传给接口的页码
      num: 1,
      // 歌词的歌
      songs: [],
      // 搜索的关键词
      searchKey: ''
    };
  },

  mounted() {
    // 初始需要从 vuex 获取关键词
    this.searchKey = store.state.searchKey; // 搜索歌词

    if (sessionStorage.getItem('searchKey')) this.getSearchLyric();
  },

  methods: {
    // 搜索歌词
    async getSearchLyric() {
      const res = await getSearchLyric(sessionStorage.getItem('searchKey'), this.limit, this.offset); // console.log('搜索歌词', res);

      this.songs = res.data.result.songs; // console.log('songs', this.songs);

      this.songCount = res.data.result.songCount;
      if (this.songs.length !== 0) this.isLoading = 0;
    },

    // 换页
    pageChange() {
      this.offset = (this.currentPage - 1) * this.limit; // console.log('当前页码：', this.currentPage, '数据偏移量：', this.offset);
      // 重新搜索歌词

      this.getSearchLyric();
    },

    // 格式化序号
    formatIndex(index) {
      return this.$formatIndex(index);
    },

    // 歌手关键词高亮显示
    activeSinger(string) {
      let reg = new RegExp(this.searchKey, 'g');
      let replaceString = '<span style="color: rgb(27, 94, 195)">' + this.searchKey + '</span>';
      string = string.replace(reg, replaceString);

      if (string.indexOf(this.searchKey) == -1) {
        this.isVHtmlSinger = 0;
        return string;
      } else {
        this.isVHtmlSinger = 1;
        this.vHtmlSinger = string;
        return '';
      }
    },

    // 格式化播放时长
    formatDuration(time) {
      return this.$formatDuration(time);
    },

    // 格式化歌词
    formatLyric(lyrics) {
      let arr; // 以换行符分割每一行

      arr = lyrics.split(/[(\r\n)\r\n]+/); // console.log(arr);

      return arr;
    },

    // 展开歌词
    openLyrics(index) {
      this.$refs.openButton1[index].style.display = 'none';
      this.$refs.openButton2[index].style.display = 'none';
      this.$refs.closeButton1[index].style.display = 'block';
      this.$refs.closeButton2[index].style.display = 'block';
      this.$refs.lyricInfo[index].style.height = 'auto';
      this.$refs.buttons2[index].style.display = 'flex';
      this.$refs.buttons2[index].style.marginTop = this.$refs.lyricBox[index].offsetHeight - 60 + 'px';
    },

    // 收起歌词
    closeLyrics(index) {
      this.$refs.openButton1[index].style.display = 'block';
      this.$refs.openButton2[index].style.display = 'block';
      this.$refs.closeButton1[index].style.display = 'none';
      this.$refs.closeButton2[index].style.display = 'none';
      this.$refs.lyricInfo[index].style.height = '86px';
    },

    // 复制歌词
    copyLyrics() {
      this.$refs.copyTips.style.opacity = '1'; // 只是隐藏了，没有销毁

      setTimeout(() => this.$refs.copyTips.style.opacity = '0', 1000);
    },

    // 去往 mv播放页
    goVideoPlayer(mvid) {
      this.$router.push({
        path: '/VideoPlayer',
        query: {
          mvid: mvid,
          type: 1
        }
      });
    }

  }
};